import {
  selectListGroup,
  selectThemeConfigGlobal,
} from '@containers/Home/selectors';
import { I_Scene } from '@containers/Home/types';
import React, {
  memo,
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useSelector } from 'react-redux';
import { ListSceneWrapper } from './styled';
import dynamic from 'next/dynamic';

const Logo = dynamic(() => import('@containers/Home/components/Logo'));
const Copyright = dynamic(() => import('./Copyright'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

export const ListScene = memo(({ onChangeScene }: Props) => {
  const containerRef = useRef<HTMLDivElement>(null!);
  const listGroup = useSelector(selectListGroup);
  const themeConfig = useSelector(selectThemeConfigGlobal);

  const [isShowListScene, setIsShowListScene] = useState(false);
  const [groupIndexSelected, setGroupIndexSelected] = useState<
    number | undefined
  >(undefined);

  const groupSelected = useMemo(
    () =>
      groupIndexSelected !== undefined
        ? listGroup.find((_, index) => index === groupIndexSelected)
        : undefined,
    [groupIndexSelected, listGroup],
  );

  const onToggleIsShowListScene = useCallback(() => {
    setIsShowListScene(prev => !prev);
  }, []);

  useEffect(() => {
    if (themeConfig?.selected === 'folio')
      setIsShowListScene(!!themeConfig?.config?.autoOpenSceneList);
  }, [themeConfig]);

  return (
    <ListSceneWrapper
      ref={containerRef}
      id="themeListScene"
      className={`${isShowListScene ? '' : 'inactive'} ${
        groupIndexSelected !== undefined ? 'sceneCate--on' : ''
      }`}
    >
      <div className="--toggler" onClick={onToggleIsShowListScene}>
        <i className="far fa-bars state-open"></i>
        <i className="far fa-times state-close"></i>
      </div>
      <div className="--mainPanel">
        <Logo />
        {(listGroup.length !== 1 ||
          (listGroup.length === 1 && listGroup[0].name !== 'Uncategory')) && (
          <div
            className={`--sceneMenuWrapper ${
              groupIndexSelected !== undefined ? 'active' : ''
            }`}
          >
            <div
              className={`sceneMenu ${
                groupIndexSelected !== undefined ? 'active' : ''
              }`}
            >
              <div className="--cateList">
                {listGroup.map((group, index) => (
                  <div
                    key={group.id}
                    className="itemSceneCate"
                    onClick={() => setGroupIndexSelected(index)}
                  >
                    {/* <div className="--cateImg">
                      <img src="https://dev-assets.panoee.com/statics/uploads/user/5fea0bb197cd73c523237485/media/pano/preview/2000x_c0k2lIy9NgNudHC19MGl.webp" alt="" />
                    </div> */}
                    <span className="--cateTitle">{group.name}</span>
                  </div>
                ))}
              </div>
              {groupIndexSelected !== undefined && (
                <div className="sceneList">
                  <span
                    className="--back"
                    onClick={() => setGroupIndexSelected(undefined)}
                  >
                    <i className="far fa-arrow-left"></i>Back
                  </span>
                  <div className="--listing">
                    {groupSelected?.scenes?.map(scene => (
                      <div
                        key={scene.id}
                        className="itemScene"
                        onClick={() => {
                          onChangeScene(scene);
                        }}
                      >
                        <img
                          src={
                            scene.screenshot ||
                            scene.media?.src_thumb ||
                            scene?.object360?.firstImage ||
                            ''
                          }
                          alt=""
                        />
                        <span>{scene.title}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
        <div className="--customLink">
          {listGroup.length === 1 &&
            listGroup[0].name === 'Uncategory' &&
            listGroup[0].scenes.map(scene => (
              <div
                key={scene.id}
                className="itemMenuLink type-scene"
                onClick={() => {
                  onChangeScene(scene);
                }}
              >
                <img
                  src={
                    scene.screenshot ||
                    scene.media?.src_thumb ||
                    scene?.object360?.firstImage ||
                    ''
                  }
                  alt=""
                />
                <span>{scene.title}</span>
              </div>
            ))}
        </div>
        <Copyright />
      </div>
    </ListSceneWrapper>
  );
});

export default ListScene;
